import Vue from 'vue';
export default (style) =>{
    return new Vue({
        data: {
            style: style
        },
        watch: {
            style(){
                this.$emit('changeStyle', this.style);
            }
        },
        methods: {
            changeColor(color){
                this.style = { ...this.style, color: color};

            },
            changeStyle(style){
                this.style = { ...this.style, ...style};
            },
            callback(func, arg){
                return this[func](arg);
            },
            getStyle(){
                return this.style;
            }
        }
    }) 
}


