
<template>
    <div id="colorpicker">
        <div  class="color-pick" @click="open" :style="{'background-color': color.hex}">
        </div>
        <div class="color-picker-module" :class="{ 'color-picker-module-open': isOpen }"><Chrome v-model="color" /></div>
        <div class="color-picker-wrapper" :class="{ 'color-picker-wrapper-open': isOpen }" @click="open"></div>
    </div>
        
</template>
<script>
import { Chrome } from 'vue-color';
export default {
    components: {
        Chrome
    },
    props: {
        color: {
            type: Object,
            default: {},
        },
    },
    data() {
        return{
            internalColor: '',
            isOpen: false
        }
    },
    created(){
    },
    watch: {
        color(){
            this.internalColor = this.color.hex;
            console.log(this.internalColor);
            this.$emit('change', this.color)
        }
    },
    computed:{
    },
    methods: {
        open(){
            if(this.isOpen){
                this.isOpen = false
            }else{
                this.isOpen = true
            }
            
        }
    }
}
</script>

<style>
#colorpicker{
    position: relative;
    margin-top: 10px;
}
.color-pick {
    width: 100%;
    height: 50px;
    background-color: #333;
    cursor: pointer;
    transition: .2s ease;
    border: 2px solid #5f5f5f;
    border-radius: 29px;
}
.color-picker-module {
    position: absolute;
    left: 10px;
    top: 58px;
    z-index: 100;
    display: none;
}
.color-picker-wrapper {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 99;
    display: none;
}
.color-picker-module-open {
    display: block;
}
.color-picker-wrapper-open {
    display: block;
}
</style>



